
          @import "src/assets/scss/_variables.scss";
        
$padding-px: 16px;

.pokemon-card {
  display: flex;
  flex-direction: column;
  font-family: 'VT323', monospace;
  width: 300px;
  height: 400px;
  border: 2px solid map-get($colors, 'primary');
  background-color: map-get($colors, 'primary-light');
  color: map-get($colors, 'lightest');
  border-radius: 16px;
  overflow: hidden;
  text-align: center;
  box-shadow: 0px 4px 10px 0px map-get($colors, 'primary');
  transition: all 0.2s ease-in-out;

  &:hover {
    box-shadow: 0px 1px 2px 0px map-get($colors, 'primary');
  }

  &__body {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    min-height: 0;
    padding: #{$padding-px} #{$padding-px} 0 #{$padding-px};
  }

  &__image {
    width: 100%;
    height: 100%;
    object-fit: contain;

    &-wrapper {
      width: 100%;
      height: 100%;
      max-width: 200px;
    }

    &--loading {
      filter: grayscale(1);
      animation: blink 1s ease-in-out infinite;
    }
  }

  &__title {
    font-size: 2rem;
    font-weight: 700;
  }

  &__footer {
    padding: $padding-px $padding-px $padding-px * 2 $padding-px;
  }
}