
          @import "src/assets/scss/_variables.scss";
        
.pokemon-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, 300px);
  grid-column-gap: 40px;
  grid-row-gap: 40px;
  grid-auto-rows: 400px;
  justify-content: center;

  &__cell {
    display: flex;
    justify-content: center;
  }
}